    details: '我叫Ye Zhong，我在Drexel University读书。今年大二，专业是金融。这是我在美国的第五年。两年前，我申请了不同的学校。因为我在美国上的高中，所以与一些没有在美国上高中的同学相比，我比较有自信。但是，我最终却上了Drexel University——一所我用来保底的学校。我对这个结果不太满意，可能因为我是完美主义者吧！于是，我决定转学到其他更好的学校。在Drexel的第一年里，我用心地去准备我的转学申请。我参加了很多社团组织，也成为了里面的Officer。另外，我还利用暑假的时间实习。我准备申请转学的那一年，刚好UC推迟了递交申请书的截止日期。我找到欧耶，认识到了Kevin（欧耶的创始人）。他帮助了我实现了我那遥不可及的转学梦。最后，欧耶帮助我利用了最后的短短三个星期时间完成申请书。也许对于完成一份申请书，仅仅三个星期的时间真的非常短且不足够。但庆幸的是，我有Kiana作为我的导师。对于申请书里的两篇自我介绍，我本是无从下手。但是Kiana每两天都抽出至少一个小时的时间去跟我谈这两篇文章的细节。我们决定把文章分为五大块。等到文章终于被写出来了之后，Kevin还亲自检查了一遍，确保一切无误。他还协助我一起填写了申请书的其他细节。那时候，Kevin人在北京。我非常感激他。因为他在和美国的我有时差，但是他都非常迁就我在美国的时间，我非常感动。有Kevin和Kiana的支持，我感到我的后背变得坚硬。整个转学过程，我充满了自信！'
