    details: '\u6211\u4ECE\u5C0F\u5C31\u5F88\u559C\u6B22\u7F8E\u56FD\u81EA\u7531\u7684\u6587\u5316\u6C1B\u56F4\uFF0C\u4E5F\u5F88\u61A7\u61AC\u7F8E\u56FD\u7684\u751F\u6D3B\u3002\u4E24\u5E74\u534A\u524D\u521A\u6765\u5230\u7F8E\u56FD\u7684\u65F6\u5019\uFF0C\u4ECE\u8BED\u8A00\u73ED\u5F00\u59CB\uFF0C\u6CA1\u6709\u4EC0\u4E48\u670B\u53CB\uFF0C\u751F\u6D3B\u4E5F\u8FC7\u5F97\u5F88\u5355\u8C03\u3002\u8F6C\u5165DVC\u4E4B\u540E\uFF0C\u5728YEAH\u7684\u6392\u8BFE\u9009\u8BFE\u5E2E\u52A9\u4E0B\uFF0C\u6211\u6162\u6162\u9002\u5E94\u4E86\u5927\u5B66\u8BFE\u7A0B\u7684\u5F3A\u5EA6\u3002\u901A\u8FC7YEAH\u987E\u95EE\u7684\u5EFA\u8BAE\uFF0C\u6211\u52A0\u5165\u4E86\u4E2D\u534E\u5B66\u751F\u4F1A\uFF0C\u5728\u90A3\u91CC\u6211\u8BA4\u8BC6\u4E86\u540E\u6765\u6700\u597D\u7684\u51E0\u4E2A\u670B\u53CB\uFF0C\u751F\u6D3B\u4E5F\u6E10\u6E10\u5145\u5B9E\u8D77\u6765\u3002\u540C\u65F6\u6211\u4E5F\u5728\u4E2D\u534E\u5B66\u751F\u4F1A\u5F97\u5230\u4E86\u6211\u7B2C\u4E00\u4E2A\u9886\u5BFC\u804C\u4F4D\u2014\u2014\u5B66\u751F\u4F1A\u8D22\u52A1\u90E8\u90E8\u957F\u3002\u6211\u662F\u4E00\u4E2A\u559C\u6B22\u6311\u6218\u7684\u4EBA\uFF0C\u5F53\u9009\u5B9A\u4E86Economics\u8FD9\u4E2A\u7ADE\u4E89\u975E\u5E38\u6FC0\u70C8\u7684\u4E13\u4E1A\u65F6\uFF0C\u6211\u5C31\u8BA4\u5B9A\u4E86\u4F2F\u514B\u5229\u4F5C\u4E3A\u6211\u7684\u76EE\u6807\u5B66\u6821\u3002\u53EF\u662F\u5F53\u65F6\u5F88\u8FF7\u832B\uFF0C\u90A3\u4E48\u591A\u4EBA\u5B66Economics\uFF0C\u81EA\u5DF1\u7684\u6210\u7EE9\u4E5F\u4E0D\u7B97\u5F88\u9AD8\uFF0C\u8DDF\u5176\u4ED6\u4EBA\u6BD4\u6CA1\u6709\u4F18\u52BF\uFF0C\u5FC3\u91CC\u4E00\u76F4\u90FD\u5F88\u7126\u8651\u3002\u540E\u6765\u53C8\u662FYEAH\u7EC4\u7EC7\u4E86\u4E00\u6B21 Berkeley Mentor Tour, \u8BA9\u6211\u8BA4\u8BC6\u4E86\u5F88\u591A\u540C\u4E13\u4E1A\u7684\u5B66\u957F\u5B66\u59D0\uFF0C\u4E5F\u4E86\u89E3\u4E86\u8FD9\u4E2A\u4E13\u4E1A\u7684\u672A\u6765\u53D1\u5C55\u65B9\u5411\uFF0C\u9700\u8981\u4EC0\u4E48\u6837\u7684\u51C6\u5907\uFF0C\u8BA9\u6211\u5FC3\u91CC\u5B89\u5B9A\u4E0D\u5C11\u3002\u57FA\u4E8E\u957F\u671F\u7684\u5173\u7CFB\u548C\u4FE1\u4EFB\uFF0C\u7533\u8BF7\u5B63\u65F6\u6211\u4E5F\u81EA\u7136\u9009\u62E9YEAH\u7EE7\u7EED\u5E2E\u52A9\u6211\u5B8C\u6210\u7533\u8BF7\u3002\u6211\u5F88\u559C\u6B22\u6211\u7684\u5BFC\u5E08\uFF0C\u4E00\u4E2A\u53CC\u5546\u90FD\u8D85\u9AD8\u7684\u97E9\u56FD\u6B27\u5DF4Brian\u3002\u4ED6\u5E2E\u6211\u7F57\u5217\u51FA\u4E86\u6211\u4EBA\u751F\u7684\u6BCF\u4E00\u4E2A\u4EAE\u70B9\uFF0C\u7136\u540E\u7EC6\u5FC3\u7B5B\u9009\uFF0C\u628A\u80FD\u60F3\u5230\u7684\u7EC6\u8282\u90FD\u5199\u51FA\u6765\uFF0C\u518D\u591A\u82B1\u65F6\u95F4\u5E2E\u6211\u4E00\u53E5\u53E5\u4FEE\u6539\u3002\u6700\u91CD\u8981\u7684\u662F\u4ED6\u8BA9\u6211\u77E5\u9053\u6211\u662F\u4E00\u4E2A\u6709\u6545\u4E8B\u7684\u4EBA\uFF0C\u800C\u4E14\u80FD\u627E\u5230\u4E00\u4E2A\u72EC\u7279\u7684\u89D2\u5EA6\u6765\u5347\u534E\u6211\u7684\u6545\u4E8B\u3002\u8BF4\u5B9E\u8BDD\uFF0C\u5728\u9047\u5230Brian\u4E4B\u524D\uFF0C\u6211\u771F\u7684\u89C9\u5F97\u81EA\u5DF1\u662F\u4E00\u4E2A\u6CA1\u6709\u6545\u4E8B\u7684\u4EBA\u3002\u867D\u7136\u6709\u65F6\u89C9\u5F97\u8981\u6293\u72C2\uFF0C\u4F46\u90A3\u6BB5\u795E\u7ECF\u9AD8\u5EA6\u7D27\u7EF7\u7684\u7533\u8BF7\u65F6\u5149\u8FD8\u662F\u86EE\u8FC7\u763E\u7684\u3002\u5728YEAH\u7684\u5E2E\u52A9\u4E0B\u6211\u4E0D\u4EC5\u5B8C\u6210\u4E86\u8FD9\u6B21\u8F6C\u5B66\u7533\u8BF7\u7684\u6311\u6218\uFF0C\u4E5F\u5F97\u5230\u4E86\u5F88\u5927\u7684\u63D0\u5347\uFF0C\u201C\u4E00\u5206\u8015\u8018\u4E00\u5206\u6536\u83B7\u201D\uFF0C\u5E0C\u671B\u672A\u6765\u81EA\u5DF1\u4E0D\u88AB\u4E8F\u5F85\u3002'
