export default [{
    id: '1',
    name: 'Ken Zhong',
    college: 'Drexel University',
    acceptBy: 'UCB, USC, UCLA, UCSD, UCD, UCI, UCSB',
    university: 'UC Berkeley',
    major: 'Statistics & Environmental Economics',
    service: '短期冲刺保录取项目',
    photo: 'https://s3-us-west-1.amazonaws.com/yeah-assets/medias/photos/temp-profile/Ken.jpg',
    content: `My dream of studying in a top college has been awaken again after I met Kevin through my friend Jason. Kevin is the founder of Year Education, which helps students to finish their transfer applications for UC Universities. It was quite amazing that UC extended their deadline of application this year, leaving me three weeks right after my final exams to finish the application with Year Education’s assistance.`,
    details: '我叫Ye Zhong，我在Drexel University读书。今年大二，专业是金融。这是我在美国的第五年。两年前，我申请了不同的学校。因为我在美国上的高中，所以与一些没有在美国上高中的同学相比，我比较有自信。但是，我最终却上了Drexel University——一所我用来保底的学校。我对这个结果不太满意，可能因为我是完美主义者吧！于是，我决定转学到其他更好的学校。在Drexel的第一年里，我用心地去准备我的转学申请。我参加了很多社团组织，也成为了里面的Officer。另外，我还利用暑假的时间实习。我准备申请转学的那一年，刚好UC推迟了递交申请书的截止日期。我找到欧耶，认识到了Kevin（欧耶的创始人）。他帮助了我实现了我那遥不可及的转学梦。最后，欧耶帮助我利用了最后的短短三个星期时间完成申请书。也许对于完成一份申请书，仅仅三个星期的时间真的非常短且不足够。但庆幸的是，我有Kiana作为我的导师。对于申请书里的两篇自我介绍，我本是无从下手。但是Kiana每两天都抽出至少一个小时的时间去跟我谈这两篇文章的细节。我们决定把文章分为五大块。等到文章终于被写出来了之后，Kevin还亲自检查了一遍，确保一切无误。他还协助我一起填写了申请书的其他细节。那时候，Kevin人在北京。我非常感激他。因为他在和美国的我有时差，但是他都非常迁就我在美国的时间，我非常感动。有Kevin和Kiana的支持，我感到我的后背变得坚硬。整个转学过程，我充满了自信！'
},{
    id: '2',    
    name: 'Brayden',
    college: 'DVC',
    acceptBy: ' USC (Marshall), UIUC, Michigan Ann Arbor (Ross), Indiana (Bloomington), UNC(Kenan-Flagler)',
    university: 'UC Berkeley',
    major: 'Haas Business',
    service: '两年制孵化器',
    photo: 'https://s3-us-west-1.amazonaws.com/yeah-assets/medias/photos/temp-profile/Brayden.jpg',
    content: `在与Kevin一起构造Personal Statement的过程中，虽然我反复地修改了四十余次，这个过程也非常痛苦，但是这也是我对我自己这两年的成长的一个反思。在这两年时间里，Kevin塑造了一个本来很自卑的男孩到一个自信的Business man的成长过程。这完完全全地反应了我这两年的内心变化。 这篇PS对我来说，不仅是一篇简简单单的Essay，而更多的是见证了我这两年的飞跃。
       半年之后，我成功地拿到了哈斯商学院的offer，成为哈斯全球招生90人中的其中一员。YEAH对于我来说不仅帮助了我完成转学、踏入哈斯的殿堂的梦想，还改变了我的人生。`,
    details: ` 我从小就是一个特别不自信、自卑的人。跟朋友之间的交流本来就很少，更不敢跟陌生人讲话。除了去学校上课以外，其他时间我都是窝在家里。两点一线，没有任何social life。我也从来都没有一种意识说要去锻炼一下自己，交朋友等。但出于哈斯商学院的向往，我决定去美国闯一闯。但是事情并没有我想得那么简单。我第一个学期的历史课就拿了个D。我当时临近崩溃的边缘，我以为哈斯就这样彻底与我无缘了。更别说以一个国际男性留学生来说，如果GPA没有4.0，进哈斯的几率几乎为0。 
       直到我遇到了Kevin（YEAH Education的创始人），加入了YEAH Education的两年长期项目。Kevin成为了我的导师，帮我规划了专业基础课的所有课程。他告诉我，这不是世界末日，还有机会的。如果重上这堂课，可以覆盖掉原来的GPA，并且在申请的时候突出自己的变化，这对我来说反倒不是一个劣势。
       Kevin了解我是一个从来不爱说话不爱表达自己的人。于是他帮我安排上了一堂中文课。而且在他的督促之下，我报名了参加中文演讲比赛。但Kevin鼓励我。他希望我不仅在学术上有一个好成绩，而且更希望我可以打开自己、懂得怎么去表达自己、面向大家、融入这个社会 。 
       在之后的一个学期，Kevin每天都会在他忙碌的生活中抽出4-5个小时的时间来帮助我练习中文。从上学到放学、从早饭到晚饭，只要Kevin有时间就陪我练习。在练习的过程中，Kevin还邀请了不少他的中国朋友来帮我练习。起初，我不太敢和他们交流。但随着他与源源不断的鼓励，我开始有勇气和这些“陌生人”说话了。而且，Kevin为了能更好地跟我沟通，他甚至花了一年的时间去学习泰文。就这样经过了4个月的艰苦训练，我终于获得了全加州中文比赛的第一名。
       这是我第一次学中文。而跟我一起竞赛的人，大都已经学了中文数年的了。而当我站在颁奖台上的那一刻，拿着奖杯，我发现这个奖杯的重量不在于奖杯本身。这是我人生拿的第一个奖。我证明了我自己！我证明了我不比别人差，而且可以比别人优秀！只要我努力，我就可以得到我想要的东西。这次比赛以后，我发现我变得有自信、开朗了。我还发现其实与人说话、交流、 了解别人是一个非常有意思、享受的过程。我更学会了如何站在别人的角度来思考问题。 
        最后在Kevin的帮助下，我参加了学校的三个社团并成为了Finance Club的副主席。一切都进行得非常顺利。我不仅以GPA4.0毕业了，而且在学校的社团里创造了很大的影响力。并且在Kevin的安排下，我获得了Deloitte的实习机会。
       在与Kevin一起构造Personal Statement的过程中，虽然我反复地修改了四十余次，这个过程也非常痛苦，但是这也是我对我自己这两年的成长的一个反思。在这两年时间里，Kevin塑造了一个本来很自卑的男孩到一个自信的Business man的成长过程。这完完全全地反应了我这两年的内心变化。 这篇PS对我来说，不仅是一篇简简单单的Essay，而更多的是见证了我这两年的飞跃。
       半年之后，我成功地拿到了哈斯商学院的offer，成为哈斯全球招生90人中的其中一员。YEAH对于我来说不仅帮助了我完成转学、踏入哈斯的殿堂的梦想，还改变了我的人生。
`   
},{
    id: '3',    
    name: 'Michelle Hu',
    college: 'DVC',
    acceptBy: 'UCB, UCSD, UCD, UCI, UCSB',
    university: 'UC Berkeley',
    major: 'Political Economics',
    service: '短期冲刺保录取项目',
    photo: 'https://s3-us-west-1.amazonaws.com/yeah-assets/medias/photos/temp-profile/Michelle.jpg',
    content: `虽然欧耶还比较年轻，但当我逐步地了解和熟悉里面的顾问和导师，我相信他们有足够的能力来帮我完成申请。直至最后，我敢肯定的是，如果当初我没有选择欧耶，我会扛不住压力，完不成申请。最后一个月冲刺写申请的时候，我也会熬夜到凌晨，每天只睡3个小时。但欧耶的导师Adriane坚持与我一起熬夜讨论和修改ps，一千字的文章在一个月里被改了不下20遍。申请的经历远没有想象中的简单，但有了欧耶的陪伴和鼓励，我坚持到了最后。`,
    details: `我自12岁起在博茨瓦纳生活。2014年来到美国后，在这里生活了将近两年，喜欢加州的天气、人文和多元化的环境。我最初并没有对社区学院抱有太大的期望，但是来了之后发现情况并没有想象中那么糟。在这里我学会了独立，交到一群知心的朋友，校园生活让从小没有在国内长大的我经历了很多以前缺失的事情。出门在外，很多事都要自己一个人承担，但从另外一个角度来说，这也让我得到了快速的成长。准备转学申请那段期间应该是每个人都拥有最大压力的时期，我敢肯定压力不会低于高考，申请那段期间我瘦了整整10斤。从遇见欧耶到最后选择欧耶我都是经过了深思熟虑的。虽然欧耶还比较年轻，但当我逐步地了解和熟悉里面的顾问和导师，我相信他们有足够的能力来帮我完成申请。直至最后，我敢肯定的是，如果当初我没有选择欧耶，我会扛不住压力，完不成申请。最后一个月冲刺写申请的时候，我也会熬夜到凌晨，每天只睡3个小时。但欧耶的导师Adriane坚持与我一起熬夜讨论和修改ps，一千字的文章在一个月里被改了不下20遍。申请的经历远没有想象中的简单，但有了欧耶的陪伴和鼓励，我坚持到了最后。`
},{
    id: '4',    
    name: 'Sunny Lee',
    college: 'DVC',
    acceptBy: 'UCB, UCLA, UCSD, UCD, UCI, UCSB',
    university: 'UC Berkeley',
    major: 'Economics',
    service: '短期冲刺保录取项目',
    photo: 'https://s3-us-west-1.amazonaws.com/yeah-assets/medias/photos/temp-profile/Sunny.jpg',
    content: `遇到了热心的学长介绍，我才知道了欧耶教育。导师Shutian给了我很大的帮助，她不仅帮我找到了自己身上可以写用来写文书的闪光点，还用过来人的经验告诉我应该突出哪些特长。她真的把我的申请当成她自己的一样，每次修改好后还专门拿去问她的同事和朋友们有什么建议。很感谢她这样帮我，我相信最初选择YEAH是正确的。`,
    details: `最初要远渡重洋来美国学习，是和爸爸妈妈一起讨论后的决定，而选择DVC是国内中介的推荐。可是当初来了之后，才发现自己真的什么都不懂，什么也不知道。之前的中介也帮不上任何忙。最开始要自己找寄宿家庭，后来又要自己找公寓住……来到了DVC才真的体验到了一个人生活的感觉。渐渐融入了美国的学习和生活后，转学申请文书又令自己陷入了一片迷茫。后来遇到了热心的学长介绍，我才知道了欧耶教育。导师Shutian给了我很大的帮助，她不仅帮我找到了自己身上可以写用来写文书的闪光点，还用过来人的经验告诉我应该突出哪些特长。她真的把我的申请当成她自己的一样，每次修改好后还专门拿去问她的同事和朋友们有什么建议。很感谢她这样帮我，我相信最初选择YEAH是正确的。`
},{
    id: '5',    
    name: 'Peter Zhou',
    college: 'DVC',
    acceptBy: 'UCB, UCSD, UCD, UCI, UCSB',
    university: 'UC Berkeley',
    major: 'Statistics + Environmental Economics',
    service: '短期冲刺保录取项目',
    photo: 'https://s3-us-west-1.amazonaws.com/yeah-assets/medias/photos/temp-profile/Peter.jpg',
    content: `虽然导师的母语是英语，但她也能很流利地讲中文。所以每一次我用英文表达不出来的时候，她都会用中文引导我，然后再用她纯熟的英文写作功底来帮我架构我的个人陈述。通过一次次与导师梳理思路、修改讨论，我的个人陈述和简历里的每一个词都经过了推敲，每一句描述都用的刚刚好。我觉得自己很幸运地能在这个阶段接受到专业的帮助，完成了高质量的文书。对我而言完成申请的过程也是一个重新发现自己、定位自己的过程。希望未来还能和YEAH有再合作的机会。`,
    details: `出国是个很偶然的决定，其实自己当时也搞不清楚自己想要什么，就这么稀里糊涂的和家里人闹着出来。但是当自己一个人拎着箱子从旧金山机场走出来的那一刻，我告诉自己，这并不是小孩子过家家，既然第一次自己做了选择，就要对自己负责。来了DVC之后，发现这里比我期望的要好，麻雀虽小五脏俱全，还是有很多惊喜的。相比国内我也有了更多可以自己分配的时间。在正式开学前，我偶然地参加了欧耶的新生讲座，才知道原来申请转学除了要有好的成绩外，还有许多东西要准备。这个讲座让我第一次了解了申请的本质，其中讲员的深度剖析给了我很多有用的信息和指导。为了两年后能成功转到我的梦想学校，我开始与YEAH合作，帮助我规划两年内需要完成的各种活动。在YEAH的顾问的指导下，我不仅加入了DVC学生政府、Unicef、CSA、Investment等社团，还成为了中文系助教，认识了很多志同道合的朋友，这也是我在DVC收获的一大财富。申请季开始后，YEAH又给我配备了专业的导师帮助我完成申请中的个人陈述。虽然导师的母语是英语，但她也能很流利地讲中文。所以每一次我用英文表达不出来的时候，她都会用中文引导我，然后再用她纯熟的英文写作功底来帮我架构我的个人陈述。通过一次次与导师梳理思路、修改讨论，我的个人陈述和简历里的每一个词都经过了推敲，每一句描述都用的刚刚好。我觉得自己很幸运地能在这个阶段接受到专业的帮助，完成了高质量的文书。对我而言完成申请的过程也是一个重新发现自己、定位自己的过程。希望未来还能和YEAH有再合作的机会。`
},{
    id: '6',    
    name: 'Zoe Ying',
    college: 'DVC',
    acceptBy: 'UCB, UCSD, UCD, UCI, UCSB',
    university: 'UC Berkeley',
    major: 'Environmental Economics',
    service: '短期冲刺保录取项目',
    photo: 'https://s3-us-west-1.amazonaws.com/yeah-assets/medias/photos/temp-profile/Zoe.jpg',
    content: `我很喜欢我的导师，一个双商都超高的韩国欧巴Brian。他帮我罗列出了我人生的每一个亮点，然后细心筛选，把能想到的细节都写出来，再多花时间帮我一句句修改。最重要的是他让我知道我是一个有故事的人，而且能找到一个独特的角度来升华我的故事。说实话，在遇到Brian之前，我真的觉得自己是一个没有故事的人。虽然有时觉得要抓狂，但那段神经高度紧绷的申请时光还是蛮过瘾的。在YEAH的帮助下我不仅完成了这次转学申请的挑战，也得到了很大的提升，“一分耕耘一分收获”，希望未来自己不被亏待。`,
    details: `我从小就很喜欢美国自由的文化氛围，也很憧憬美国的生活。两年半前刚来到美国的时候，从语言班开始，没有什么朋友，生活也过得很单调。转入DVC之后，在YEAH的排课选课帮助下，我慢慢适应了大学课程的强度。通过YEAH顾问的建议，我加入了中华学生会，在那里我认识了后来最好的几个朋友，生活也渐渐充实起来。同时我也在中华学生会得到了我第一个领导职位——学生会财务部部长。我是一个喜欢挑战的人，当选定了Economics这个竞争非常激烈的专业时，我就认定了伯克利作为我的目标学校。可是当时很迷茫，那么多人学Economics，自己的成绩也不算很高，跟其他人比没有优势，心里一直都很焦虑。后来又是YEAH组织了一次 Berkeley Mentor Tour, 让我认识了很多同专业的学长学姐，也了解了这个专业的未来发展方向，需要什么样的准备，让我心里安定不少。基于长期的关系和信任，申请季时我也自然选择YEAH继续帮助我完成申请。我很喜欢我的导师，一个双商都超高的韩国欧巴Brian。他帮我罗列出了我人生的每一个亮点，然后细心筛选，把能想到的细节都写出来，再多花时间帮我一句句修改。最重要的是他让我知道我是一个有故事的人，而且能找到一个独特的角度来升华我的故事。说实话，在遇到Brian之前，我真的觉得自己是一个没有故事的人。虽然有时觉得要抓狂，但那段神经高度紧绷的申请时光还是蛮过瘾的。在YEAH的帮助下我不仅完成了这次转学申请的挑战，也得到了很大的提升，“一分耕耘一分收获”，希望未来自己不被亏待。`
}]


