import React, { Component } from 'react';
import { ComponentLeader, Loader } from '../widgets';
import Stories from '../home/stories/';

const data = require('./data').default;


class StoriesDetails extends Component {

    constructor (props) {
        super(props);

        this.id = 1;
    }

    componentWillMount() {
        if (this.props.props) {
            this.props = this.props.props;
        }

        const pathName = this.props.location.pathname;
        this.id = pathName.split('/').pop();
    }

    render() {
        if (data) {
            const story = data[this.id-1];
            return (
            <div className="">
                <ComponentLeader title="Successful Story"/>
                <div className="stories-section">
                    <div className="stories-main">
                        <div className="stories-header">
                            <div>
                                <img alt="" src={story.photo} />
                            </div>
                            <div className="stories-description">
                                <h5>{story.name}</h5>
                                <div className="stories-university">{story.university}, &nbsp;{story.major} Major</div>
                                <div className="stories-service">{story.service}</div>
                                <div className="stories-accepted">Got other admissions: &nbsp; {story.acceptBy}</div>
                            </div>
                        </div>
                        <div className="story-content">
                            <p>
                                {story.details}
                            </p>
                        </div>
                    </div>
                </div>
                <div className="stories">
                    <Stories />                
                </div>
            </div>
        ); } else {
            return <Loader />
        }
    }
};

export default StoriesDetails;


